.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10%;
  height: 50vh;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.row-reset-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 25px;
  margin-top: 20px;
}

.row-textbox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: x-large;
  min-height: 50px;
}

.tile {
  display: flex;
  min-height: 100px;
  height: 100px;
  width: 100px;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: xxx-large;
}
.btn {
  width: 100%;
  height: 100%;
  background-color: white;
  font-size: larger;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: black;
  padding: 0%;
  border: solid 1px black;
}
.btn-reset {
  color: black;
  font-size: medium;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
